import { NavLink } from "react-router-dom";
import "./navbar.css";
const Navbar = () => {
  return (
    <>
      <nav className="navbar">
        <NavLink
          to="/Shop_Dog"
          activeStyle={{
            fontWeight: "bold",
            color: "red",
          }}
        >
          Home
        </NavLink>
        <NavLink
          to="/dogs"
          activeStyle={{
            fontWeight: "bold",
            color: "red",
          }}
        >
          Dogs
        </NavLink>
        <NavLink
          to="/cart"
          activeStyle={{
            fontWeight: "bold",
            color: "red",
          }}
        >
          My Cart
        </NavLink>
      </nav>
    </>
  );
};

export default Navbar;
