import { useContext, useState } from "react";
import { CartContext } from "../../Context/CartContext";
import "./dog.css";
const DogsCard = (props) => {
  const { name, breed, description, price, imageUrl } = props;
  const [isAdded, setAdded] = useState(false);
  const { setMyCart, setTotal } = useContext(CartContext);
  const handleClick = () => {
    setAdded(true);
    const newItem = {
      name: name,
      price: price,
      imageUrl: imageUrl,
    };
    setMyCart((item) => [...item, newItem]);
    setTotal((total) => (total += Number(price)));
  };
  return (
    <>
      <section className="dogs">
        <div className="dogs-info">
          <p>{name}</p>
          <p>{breed}</p>
        </div>
        <div className="dogs-img-container">
          <img className="dog-img" src={imageUrl} alt={name} />
        </div>
        <div className="dogs-desc">{description}</div>
        <div className="dogs-price">{price}</div>
        {isAdded ? (
          <button className="dogs-btn-disabled">ADDED</button>
        ) : (
          <button className="dogs-btn" onClick={handleClick}>
            ADD TO CART
          </button>
        )}
      </section>
    </>
  );
};

export default DogsCard;
