import DogsCard from "./DogsCard";

const Dogs = ({ allDogs }) => {
  return (
    <>
      <section className="dogs-container">
        {allDogs.map((dog) => (
          <div key={dog.id}>
            <DogsCard
              id={dog.id}
              name={dog.name}
              breed={dog.breed}
              description={dog.description}
              price={dog.price}
              imageUrl={dog.imageUrl}
            />
          </div>
        ))}
      </section>
    </>
  );
};

export default Dogs;
